import { defineComponent, computed, ref, watch } from 'vue';
import LbTable from '@/base-ui/table';
import { usePermission } from '@/hooks/usePermission';
import { useStore } from '@/store';
export default defineComponent({
  props: {
    contentTableConfig: {
      type: Object,
      required: true
    },
    pageName: {
      type: String,
      required: true
    }
  },
  components: {
    LbTable
  },
  emits: ['newBtnClick', 'updateBtnClick'],

  setup(props, {
    emit
  }) {
    const store = useStore(); // 0.获取操作权限

    const isCreate = usePermission(props.pageName, 'create');
    const isUpdate = usePermission(props.pageName, 'update');
    const isDelete = usePermission(props.pageName, 'delete');
    const isQuery = usePermission(props.pageName, 'query'); // 1.页面底部的分页信息,双向绑定pageInfo

    const pageInfo = ref({
      currentPage: 1,
      pageSize: 10
    }); // 监听pageInfo的变化,触发获取数据的方法

    watch(pageInfo, () => {
      getPageData();
    }, {
      deep: true
    }); // 2.发送网络请求获取数据

    const getPageData = (queryInfo = {}) => {
      if (!isQuery) return;
      store.dispatch('system/getPageListAction', {
        pageName: props.pageName,
        queryInfo: {
          // 偏移多少条数据
          offset: (pageInfo.value.currentPage - 1) * pageInfo.value.pageSize,
          // 每页多少条数据
          size: pageInfo.value.pageSize,
          ...queryInfo
        }
      });
    };

    getPageData(); // 3.从vuex中获取数据

    const dataList = computed(() => store.getters[`system/pageListData`](props.pageName));
    const dataCount = computed(() => store.getters[`system/pageListCount`](props.pageName)); // 4.获取其他的动态插槽名称

    const otherProPSlots = props.contentTableConfig?.propList.filter(item => {
      if (item.slotName === 'status') return false;
      if (item.slotName === 'createAt') return false;
      if (item.slotName === 'updateAt') return false;
      if (item.slotName === 'handler') return false;
      return true;
    }); // 5.处理表格中的事件(选中,编辑,删除)
    // 监听分页信息的变化

    const handleSelectionChange = value => {
      console.log(value);
    }; // 新建


    const handleNewClick = () => {
      emit('newBtnClick');
    }; // 编辑


    const handleUpdateClick = row => {
      emit('updateBtnClick', row);
    }; // 删除


    const handleDeleteClick = row => {
      console.log(row);
      store.dispatch('system/deletePageDataAction', {
        pageName: props.pageName,
        id: row.id
      });
    };

    return {
      dataList,
      dataCount,
      getPageData,
      otherProPSlots,
      handleSelectionChange,
      pageInfo,
      isCreate,
      isUpdate,
      isDelete,
      isQuery,
      handleNewClick,
      handleUpdateClick,
      handleDeleteClick
    };
  }

});